import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Paper,
  TextField,
  Button,
  Typography,
  Box,
  Alert,
} from '@mui/material';
import { useForm } from 'react-hook-form';

const Login = () => {
  const navigate = useNavigate();
  const [error, setError] = useState('');
  const { register, handleSubmit, formState: { errors } } = useForm();

  const onSubmit = (data) => {
    // Simple authentication - in a real app, this would validate against a backend
    if (data.anvndarNamn === 'you' && data.lsenord === 'password') {
      // Store user session
      localStorage.setItem('isLoggedIn', 'true');
      localStorage.setItem('currentUser', data.anvndarNamn);
      navigate('/start');
    } else {
      setError('Felaktigt anvndarnamn eller lsenord');
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <Box
        sx={{
          marginTop: 8,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        }}
      >
        <Paper elevation={3} sx={{ padding: 4, width: '100%' }}>
          <Typography component="h1" variant="h4" align="center" gutterBottom>
            RB rendesystem
          </Typography>
          <Typography component="h2" variant="h6" align="center" color="textSecondary" gutterBottom>
            Logga in
          </Typography>
          
          {error && (
            <Alert severity="error" sx={{ mb: 2 }}>
              {error}
            </Alert>
          )}
          
          <Box component="form" onSubmit={handleSubmit(onSubmit)} sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="anvndarNamn"
              label="Anvndarnamn"
              name="anvndarNamn"
              autoComplete="username"
              autoFocus
              {...register('anvndarNamn', { required: 'Anvndarnamn krvs' })}
              error={!!errors.anvndarNamn}
              helperText={errors.anvndarNamn?.message}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="lsenord"
              label="Lsenord"
              type="password"
              id="lsenord"
              autoComplete="current-password"
              {...register('lsenord', { required: 'Lsenord krvs' })}
              error={!!errors.lsenord}
              helperText={errors.lsenord?.message}
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Logga in
            </Button>
          </Box>
          
          <Typography variant="body2" color="textSecondary" align="center" sx={{ mt: 2 }}>
            Test anvndarnamn: <strong>you</strong><br />
            Test lsenord: <strong>password</strong>
          </Typography>
        </Paper>
      </Box>
    </Container>
  );
};

export default Login;
